import { ChangeDetectionStrategy } from '@angular/compiler';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiService } from 'src/app/services/api.service';
import { DataService } from 'src/app/services/data.service';
import { FingerprintAIO } from '@ionic-native/fingerprint-aio/ngx';
import { Platform, ToastController } from '@ionic/angular';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-securitycenter',
  templateUrl: './securitycenter.page.html',
  styleUrls: ['./securitycenter.page.scss'],
})
export class SecuritycenterPage implements OnInit {
  title: string = 'Security Center';
  mpin: string;
  setmpinFlag: boolean = true;
  currentUser: any;
  isFaceId: boolean = false;
  isFingerPrint: boolean = false;
  isDevice: boolean;
  bioForm: FormGroup;

  constructor(private router: Router,
    private cdr: ChangeDetectorRef,
    private dataService: DataService,
    private toastController: ToastController,
    private faio: FingerprintAIO,
    public platform: Platform,
    private fb: FormBuilder,
    private apiProvider: ApiService) { }

  ngOnInit() {
    this.buildBioAuthStateForm();
    this.currentUser = this.dataService.getCurrentUser();
    this.isDevice = this.platform.is('cordova');


    this.isFaceId = this.currentUser?.faceLoginEnable ? true : false;
    this.isFingerPrint = this.currentUser?.fingerprintLoginEnable ? true : false;
    this.setmpinFlag = this.currentUser?.mpinActive ? true : false;

    this.bioForm.get('isFaceId').patchValue(this.isFaceId);
    this.bioForm.get('isFingerPrint').patchValue(this.isFingerPrint);

    this.onChanges();

    this.cdr.markForCheck();
  }

  buildBioAuthStateForm() {
    this.bioForm = this.fb.group({
      isFaceId: [false, Validators.required],
      isFingerPrint: [false, Validators.required]
    });
  }

  ionViewWillEnter() {

  }


  goToHome() {
    this.router.navigate(['/profile']);
  }

  setmpin() {
    this.router.navigate(['/setmpin']);
  }

  changempin() {
    this.router.navigate(['/changempin']);
  }

  changePassword() {
    this.router.navigate(['/changepassword']);
  }
  goToLoginActivity() {
    this.router.navigate(['/loginactivity']);
  }

  onChanges() {
    let customerId = this.currentUser?.customerId;
    this.bioForm.get('isFaceId').valueChanges.subscribe(res => {
      if (res) {
        this.faio.isAvailable().then((result: any) => {
          if (result === "face" || result === "biometric") {
            let payload = {
              customerId: customerId,
              faceLoginEnable: this.bioForm.get('isFaceId')?.value,
              fingerprintLoginEnable: this.bioForm.get('isFingerPrint')?.value
            };

            this.apiProvider.registerFaceIdORFingerPrint(payload).subscribe((res: any) => {
              localStorage.setItem(
                'customer_details',
                JSON.stringify(res.data)
              );
              this.dataService.isLoggedIn.next(true);
              this.presentToast("Biometric Authentication has been Enabled!");
            }, err => {
              console.log(err);
            });

          } else {
            this.presentToast("Biometric Authentication Not Available in your device!");
            this.bioForm.get('isFaceId')?.patchValue(false);
          }
          this.cdr.markForCheck();
        }).catch((error: any) => {
          this.presentToast("Biometric Authentication Not Available in your device!");
          this.bioForm.get('isFaceId')?.patchValue(false);
        });
      } else {
        console.log("update");
        let payload = {
          customerId: customerId,
          faceLoginEnable: this.bioForm.get('isFaceId')?.value,
          fingerprintLoginEnable: this.bioForm.get('isFingerPrint')?.value
        };

        this.apiProvider.registerFaceIdORFingerPrint(payload).subscribe((res: any) => {
          localStorage.setItem(
            'customer_details',
            JSON.stringify(res.data)
          );
          this.dataService.isLoggedIn.next(true);
          this.presentToast("Biometric Authentication has been Enabled!");
          this.cdr.markForCheck();
        }, err => {
          console.log(err);
        });

      }
    });

    this.bioForm.get('isFingerPrint').valueChanges.subscribe(res => {
      if (res) {
        this.faio.isAvailable().then((result: any) => {
          if (result === "face" || result === "biometric") {
            let payload = {
              customerId: customerId,
              faceLoginEnable: this.bioForm.get('isFaceId')?.value,
              fingerprintLoginEnable: this.bioForm.get('isFingerPrint')?.value
            };

            this.apiProvider.registerFaceIdORFingerPrint(payload).subscribe((res: any) => {
              localStorage.setItem(
                'customer_details',
                JSON.stringify(res.data)
              );
              this.dataService.isLoggedIn.next(true);
              this.presentToast("Biometric Authentication has been Enabled!");
            }, err => {
              console.log(err);
            });

          } else {
            this.presentToast("Biometric Authentication Not Available in your device!");
            this.bioForm.get('isFingerPrint')?.patchValue(false);
          }
          this.cdr.markForCheck();
        }).catch((error: any) => {
          this.presentToast("Biometric Authentication Not Available in your device!");
          this.bioForm.get('isFingerPrint')?.patchValue(false);
          this.cdr.markForCheck();
        });
      } else {
        console.log("update");
        let payload = {
          customerId: customerId,
          faceLoginEnable: this.bioForm.get('isFaceId')?.value,
          fingerprintLoginEnable: this.bioForm.get('isFingerPrint')?.value
        };

        this.apiProvider.registerFaceIdORFingerPrint(payload).subscribe((res: any) => {
          localStorage.setItem(
            'customer_details',
            JSON.stringify(res.data)
          );
          this.dataService.isLoggedIn.next(true);
          this.presentToast("Biometric Authentication has been Enabled!");
          this.cdr.markForCheck();
        }, err => {
          console.log(err);
        });
      }
    });

  }


  async presentToast(msg: any, position?: any) {
    const toast = await this.toastController.create({
      message: msg,
      duration: 800,
      position: position || 'bottom'
    });
    toast.present();
  }

}
